import React, { useState } from "react";
import "./ItemsList.css";
import { ReactComponent as Create } from "../assets/create.svg";
import EditQuantitiesMenu from "./EditQuantitiesMenu";

const ItemsList = ({
  itemlist,
  deleteButtonHandler,
  reduceButtonHandler,
  increaseButtonHandler,
}) => {
  const [quantitiesMode, setQuantitiesMode] = useState({});

  function closeEditMode(name) {
    setQuantitiesMode({ ...quantitiesMode, [name]: false });
  }

  return (
    <div className="shopping-list-container">
      <div className="shopping-list-title">
        <h1>Shopping List</h1>
        <Create />
      </div>

      {itemlist.map((i, idx) => {
        return (
          <div key={idx} className="shopping-items-container">
            <h1 className="shopping-list-category">{i.category}</h1>
            {i.items.map((i, idx) => (
              <div key={idx} className="shopping-item-container">
                <p className="shopping-item">{i.name}</p>
                {quantitiesMode[i.name] ? (
                  <EditQuantitiesMenu
                    name={i.name}
                    quantity={i.quantity}
                    closeEditMode={closeEditMode}
                    deleteButtonHandler={deleteButtonHandler}
                    reduceButtonHandler={reduceButtonHandler}
                    increaseButtonHandler={increaseButtonHandler}
                  />
                ) : (
                  <button
                    onClick={() =>
                      setQuantitiesMode({ ...quantitiesMode, [i.name]: true })
                    }
                    className="shopping-quantity-button"
                  >
                    {i.quantity} pcs
                  </button>
                )}
              </div>
            ))}
          </div>
        );
      })}
    </div>
  );
};
export default ItemsList;
