import React from "react";
import "./EditQuantitiesMenu.css";
import { ReactComponent as Delete } from "../assets/delete.svg";
import { ReactComponent as Reduce } from "../assets/reduce.svg";
import { ReactComponent as Add } from "../assets/add.svg";

const EditQuantitiesMenu = ({
  quantity,
  name,
  closeEditMode,
  deleteButtonHandler,
  reduceButtonHandler,
  increaseButtonHandler,
}) => {
  return (
    <div className="edit-menu-container">
      <button
        onClick={() => deleteButtonHandler(name)}
        className="delete-item-button"
      >
        <Delete />
      </button>

      <Reduce
        onClick={() => reduceButtonHandler(name)}
        className="remove-icon"
        fill="#F9A109"
      />

      <button
        onClick={() => closeEditMode(name)}
        className="shopping-quantity-button"
      >
        {quantity} pcs
      </button>
      <Add
        onClick={() => increaseButtonHandler(name)}
        className="add-icon"
        fill="#F9A109"
      />
    </div>
  );
};
export default EditQuantitiesMenu;
