import React from "react";
import "./Items.css";
import ItemsCategory from "./ItemsCategory";

const Items = ({ data, itemDetailsHandler }) => {
  return (
    <div className="items-container">
      {data.length > 0 &&
        data.map((item, i) => {
          return (
            <ItemsCategory
              itemDetailsHandler={itemDetailsHandler}
              key={i}
              {...item}
            />
          );
        })}
    </div>
  );
};
export default Items;
